.form-wrap {
  width: 100%;
  margin: 0px auto;
  padding: 50px 20px;
  padding: 50px 20px;
  background-color: #aad6d6;
  min-height:90vh;

}
.form {
  width: 35%;
  margin: 0px auto;
  display: flex;
  flex-direction: column;
}
.form input {
  margin-bottom: 25px;
  padding: 10px 15px;
  border-radius: 8px;
}
.form button {
  margin-bottom: 25px;
  padding: 10px 15px;
  border-radius: 8px;
  background-color:#39b8e4c9;
}
.action_block{
  display: flex;
  justify-content: space-between;
}
.title{
    height: 40px;
    padding: 10px 15px; 
    position: fixed;
    top: 10px;
    right: 45%;
}