.users-list {
  height:90vh;
  background-color: #aad6d6;
  padding: 20px 40px;
  display: grid;
  grid-template-columns: 2fr 2fr 2fr 2fr;
}
.user-item {
  border: 2px solid gray;
  border-radius: 15px;
  padding: 10px 20px;
  margin: 10px;
  background-color: #5f9ea0;
}
.getBtn{
    width: 150px;
    height: 40px;
    padding: 10px 15px;
    border: 1px solid gray;
    background-color: #9811521a;
    position: fixed;
    top: 10px;
    right: 45%;
}
